import { Avatar, Button, Card, CardBody, CardFooter, CardHeader, Divider, Dropdown, DropdownItem, DropdownMenu, DropdownTrigger, Link, Navbar, NavbarBrand, NavbarContent, NavbarItem, Radio, RadioGroup, Spinner, Tab, Tabs } from "@nextui-org/react";
import { User } from "../models/user";
import * as UserApi from "../network/user_api"
import { useEffect, useState } from "react";

interface DashboardLoggedInViewProps {
    loggedInUser: User | null
}

const DashboardLoggedInView = (props: DashboardLoggedInViewProps) => {
    const [dashboardLoading, setDashboardLoading] = useState(true);
    const [user, setUser] = useState<User>({

        username: "None",
        email: "None",
        socialScienceQuestionsAnswered: 0,
        socialScienceQuestionsCorrect: 0,
        scienceQuestionsAnswered: 0,
        scienceQuestionsCorrect: 0,
        artQuestionsAnswered: 0,
        artQuestionsCorrect: 0,
        litQuestionsAnswered: 0,
        litQuestionsCorrect: 0,
        econQuestionsAnswered: 0,
        econQuestionsCorrect: 0,
        mathQuestionsAnswered: 0,
        mathQuestionsCorrect: 0,
        musicQuestionsAnswered: 0,
        musicQuestionsCorrect: 0,

    });

    const [highestPerformanceCategory, setHighestPerformanceCategory] = useState("Social Science");
    const [highestCategoryQuestionsCorrect, sethighestCategoryQuestionsCorrect] = useState<Number>(0);
    const [lowestPerformanceCategory, setLowestPerformanceCategory] = useState("Social Science");
    const [lowestPerformanceQuestionsCorrect, setLowestPerformanceQuestionsCorrect] = useState<Number>(0);
    const [questionsCorrect, setQuestionsCorrect] = useState(0);

    function findHighestCategory(selectedUser : User) {
        let highestValue = selectedUser.socialScienceQuestionsAnswered ?? 0;
        let category = "Social Science";

        if (selectedUser.artQuestionsCorrect > highestValue){
            category = "Art";
            highestValue = selectedUser.artQuestionsCorrect;
            
        }

        if (selectedUser.econQuestionsCorrect > highestValue){
            category = "Economics";
            highestValue = selectedUser.econQuestionsCorrect;
        }

        if (selectedUser.scienceQuestionsCorrect > highestValue){
            category = "Science";
            highestValue = selectedUser.scienceQuestionsCorrect;
        }

        if (selectedUser.litQuestionsCorrect > highestValue){
            category = "Literature";
            highestValue = selectedUser.litQuestionsCorrect;
        }

        if (selectedUser.mathQuestionsCorrect > highestValue){
            category = "Math";
            highestValue = selectedUser.mathQuestionsCorrect;
        }

        if (selectedUser.musicQuestionsCorrect > highestValue){
            category = "Music";
            highestValue = selectedUser.musicQuestionsCorrect;
        }

        sethighestCategoryQuestionsCorrect(highestValue);
        return category;
    }

    function findLowestCategory(selectedUser : User) {
        let lowestValue = selectedUser.socialScienceQuestionsCorrect ?? 0;
        let category = "Social Science";

        console.log(selectedUser.artQuestionsAnswered);
        
        let a = (selectedUser.artQuestionsCorrect ?? 0);
        if (a < lowestValue){
            category = "Art";
            lowestValue = a;
        }


        a = (selectedUser.econQuestionsCorrect ?? 0);
        if (a < lowestValue){
            category = "Economics";
            lowestValue = a;
        }

        a = (selectedUser.scienceQuestionsCorrect ?? 0);
        if (a < lowestValue){
            category = "Science";
            lowestValue = a;
        }

        a = (selectedUser.litQuestionsCorrect ?? 0);
        if (a < lowestValue){
            category = "Literature";
            lowestValue = a;
        }

        a = (selectedUser.mathQuestionsCorrect ?? 0);
        if (a < lowestValue){
            category = "Math";
            lowestValue = a;
        }

        a = (selectedUser.musicQuestionsCorrect ?? 0);
        if (a < lowestValue){
            category = "Music";
            lowestValue = a;
        }

        setLowestPerformanceQuestionsCorrect(lowestValue);
        return category;
    }

    function sumCorrectAnswers(selectedUser: User) {
        let a = 0;

        a += (selectedUser.artQuestionsCorrect ?? 0) as number;
        a += (selectedUser.econQuestionsCorrect ?? 0) as number;
        a += (selectedUser.scienceQuestionsCorrect ?? 0) as number;
        a += (selectedUser.litQuestionsCorrect ?? 0) as number;
        a += (selectedUser.mathQuestionsCorrect ?? 0) as number;
        a += (selectedUser.musicQuestionsCorrect ?? 0) as number;
        a += (selectedUser.socialScienceQuestionsCorrect ?? 0) as number;

        return a;
    }

    async function loadUser(){
        try {
            setDashboardLoading(true);
  
            let userInfo = null;
            if (props.loggedInUser){
                userInfo = await UserApi.getUserStats({username:props.loggedInUser.username});
                console.log(userInfo);
            }else{
                throw new Error("No user logged in");
            }
            

            await setHighestPerformanceCategory(findHighestCategory(userInfo));
            await setLowestPerformanceCategory(findLowestCategory(userInfo));
            await setQuestionsCorrect(sumCorrectAnswers(userInfo));
            
            console.log(userInfo);
            console.log(highestCategoryQuestionsCorrect);
            
            
            
            setUser(userInfo ?? {username: "None", email: "None", socialScienceQuestionsAnswered: 0, socialScienceQuestionsCorrect: 0,});
        } catch (error) {
          console.log(error);
        } finally {
            setDashboardLoading(false);
        }
        
      }
        
    useEffect(() => {
        loadUser();
    }, []);
    
    return ( 
        
        
            <>
            
            {dashboardLoading && <Spinner color="secondary" style={{marginLeft:"auto", marginRight:"auto", justifyContent:"center", display:"flex"}}/>}
            { !dashboardLoading && <><div className="max-w-md" style={{marginRight:"auto", marginLeft:"auto"}}>
                    <div className="space-y-1">
                    <Avatar
                                showFallback
                                style={{display:"inline-block", marginLeft:"auto", marginRight:"2rem"}}
                                name={user.username}
                                src=""
                            />
                        <div className="inline-block">
                        
                            <h4 className="text-medium font-medium" style={{display:"inline-block"}}>{user.username}</h4>
                            
                        </div>
                        
                        <p className="text-small text-default-400">An Academic Weapon (Probably)</p>
                        
                    </div>
                    
                    <Divider className="my-4" />
                </div>

                <div className="md:flex" style={{display:"flex", justifyContent:"center"}}>
                    
                    <Card style={{marginLeft:"auto", marginRight:"4rem", maxWidth:"18rem", height:"20rem"}}>
                        <CardHeader className="flex gap-3">
                            <div className="flex flex-col" style={{display:"auto"}}>
                            <p className="text-md" style={{marginLeft:"0.5rem"}}>Top Category</p>
                                <Dropdown>
                                    <DropdownTrigger>
                                        <Button style={{marginTop:"1rem", marginLeft:"0rem"}}
                                        variant="shadow">
                                        Ordered By . . .
                                        </Button>
                                    </DropdownTrigger>
                                    <DropdownMenu aria-label="Static Actions">
                                        <DropdownItem key="byQuestionsCorrect" className="text-md" style={{color:"white"}}>By Questions Correct</DropdownItem>
                                        <DropdownItem key="byCorrectRatio" style={{color:"white"}}>By Correct/Incorrect Ratio</DropdownItem>
                                    </DropdownMenu>
                                </Dropdown>
                            </div>
                        </CardHeader>
                        <Divider/>
                        <CardBody>
                            <h2 style={{justifyContent:"center", marginLeft:"auto", marginRight:"auto", fontSize:45, marginTop:"auto", marginBottom:"0.6rem"}}>{highestPerformanceCategory}</h2>
                        </CardBody>
                        <Divider/>
                        <CardFooter>
                            <p>{highestCategoryQuestionsCorrect.toString()} Questions</p>
                        </CardFooter>
                    </Card>
                    
                    <Card style={{marginLeft:"0", marginRight:"auto", width:"18rem", marginBottom:"auto", height:"20rem"}}>
                        <CardHeader className="flex gap-3">
                            <div className="flex flex-col" style={{display:"auto"}}>
                            <p className="text-md" style={{marginLeft:"0.5rem"}}>Worst Category</p>
                                <Dropdown>
                                    <DropdownTrigger>
                                        <Button style={{marginTop:"1rem", marginLeft:"0rem"}}
                                        variant="shadow">
                                        Ordered By . . .
                                        </Button>
                                    </DropdownTrigger>
                                    <DropdownMenu aria-label="Static Actions">
                                        <DropdownItem key="byQuestionsCorrect" className="text-md" style={{color:"white"}}>By Questions Correct</DropdownItem>
                                        <DropdownItem key="byCorrectRatio" style={{color:"white"}}>By Correct/Incorrect Ratio</DropdownItem>
                                    </DropdownMenu>
                                </Dropdown>
                            </div>
                        </CardHeader>
                        <Divider/>
                        <CardBody>
                            <h2 style={{justifyContent:"center", marginLeft:"auto", marginRight:"auto", fontSize:45, marginTop:"auto", marginBottom:"0.6rem"}}>{lowestPerformanceCategory}</h2>
                        </CardBody>
                        <Divider/>
                        <CardFooter>
                        <p style={{textAlign:"center", alignContent:"center"}}>{lowestPerformanceQuestionsCorrect.toString()} Questions</p>
                        </CardFooter>
                    </Card>
                </div>

                <Card style={{marginLeft:"auto", marginRight:"auto", width:"40rem", marginTop:"1rem", marginBottom:"auto", maxHeight:"20rem"}}>
                        <CardHeader className="flex gap-3">
                            <div className="flex flex-col" style={{display:"auto"}}>
                            <p className="text-md" style={{marginLeft:"0.5rem"}}>Category Of The Day</p>
                            </div>
                        </CardHeader>
                        <Divider/>
                        <CardBody>
                            <h2 style={{justifyContent:"center", marginLeft:"auto", marginRight:"auto", fontSize:45, marginTop:"auto", marginBottom:"0.6rem"}}>Economics</h2>
                        </CardBody>
                        <Divider/>
                        <CardFooter>
                        <p style={{textAlign:"center", alignContent:"center"}}>Answer more of these questions To be cool or something</p>
                        </CardFooter>
                    </Card> </>}
            
            
        
        </>
     );
}

