import { useForm } from "react-hook-form";
import { User } from "../models/user";
import { SignUpCredentials } from "../network/user_api";
import * as UserApi from "../network/user_api";
import {Modal, ModalContent, ModalFooter, ModalHeader, Button, ModalBody, Input, useDisclosure} from "@nextui-org/react"
import TextInputField from "./form/TextInputField";
import styles from "../styles/utils.module.css";
import { useState } from "react";

interface SignUpModalProps {
    onDismiss: () => void,
    onSignUpSuccessful: (user : User) => void
}


const SignUpModal = ({onDismiss, onSignUpSuccessful}: SignUpModalProps) => {

    const [emailInput, setEmailInput] = useState<string>();
    const [usernameInput, setusernameInput] = useState("");
    const [passwordInput, setPasswordInput] = useState("");
    const [submitting, setSubmitting] = useState(false);
    
    const [usernameError, setusernameError] = useState("");
    const [emailError, setEmailError] = useState("");
    const [passwordError, setPasswordError] = useState("");
    const [generalError, setGeneralError] = useState("");

    async function onSubmit(credentials : SignUpCredentials) {
        console.log("submitting");
        setSubmitting(true);

        try {
            const newUser = await UserApi.signup(credentials);
            onSignUpSuccessful(newUser);
            console.log("submitted successfully");

        } catch (error: any) {
            setGeneralError(error?.message || "An error occurred");
            console.log(error);
        } finally {
            setSubmitting(false);
        }
    }

    return (
        <>
        <Modal isOpen={true} onClose={onDismiss}>
        <ModalContent>
          {(onClose) => (
            <>
              <ModalHeader className="flex flex-col gap-1" style={{color:"white"}}>Sign Up</ModalHeader>
              <ModalBody>
              <Input isInvalid={(emailError != "")} errorMessage={emailError != "" && (emailError)} type="email" label="Email" placeholder="Enter your email" required onChange={(e) => {setEmailInput(e.target.value); setEmailError("");}} />
              <Input isInvalid={(usernameError != "")} errorMessage={usernameError != "" && (usernameError)} type="username" label="Username" placeholder="Enter your username" required onChange={(e) => {setusernameInput(e.target.value); setusernameError("");}} />
              <Input isInvalid={(passwordError != "")} errorMessage={passwordError != "" && (passwordError)} type="password" label="Password" placeholder="Enter your password" required onChange={(e) => {setPasswordInput(e.target.value); setPasswordError("");}} />

              </ModalBody>
              <ModalFooter>
                <Button color="danger" variant="light" onPress={onDismiss}>
                  Close
                </Button>
                <Button isLoading={submitting} color="primary" onPress={() => {

                    let emailRegex = /\S+@\S+\.\S+/;
                    let issue = false;

                    if (emailInput == ""){
                        setEmailError("Email cannot be empty");
                        issue = true;
                    }

                    if (emailInput != "" && !emailRegex.test(emailInput as string)){
                        setEmailError("Invalid email");
                        issue = true;
                    }

                    if (usernameInput == ""){
                        setusernameError("Username cannot be empty");
                        issue = true;
                    }

                    if (passwordInput == ""){
                        setPasswordError("Password cannot be empty");
                        issue = true;
                    }

                    if (issue){
                        return;
                    }
                    
                    onSubmit({ email: emailInput || "", username: usernameInput, password: passwordInput });

                }}>
                  Sign Up
                </Button>
              </ModalFooter>
            </>
          )}
        </ModalContent>
      </Modal>
        </>

        

    );
} 

