import { useEffect, useState } from "react";
import {CardBody, CardHeader, Toast } from "react-bootstrap";
import {Spinner, Card, Divider, Avatar} from "@nextui-org/react";
import { Question as QuestionModel } from '../models/question';
import Question from "./Question";
import * as UserApi from "../network/user_api";
import { User } from "../models/user";

interface QuestionPageLoggedInProps {
  user : User,
  category: string,
  questionCount: number,
  liveCorrections: Boolean,
  showPageNum: Boolean,
}

const QuestionPageLoggedInView = (props : QuestionPageLoggedInProps) => {
    const [question, setQuestion] = useState<QuestionModel>({
        "question": "LOADING QUESTION",
        "answerChoiceA": "ANSWER A",
        "answerChoiceB": "ANSWER B",
        "answerChoiceC": "ANSWER C",
        "answerChoiceD": "ANSWER D",
        "answerChoiceE": "ANSWER E",
        "correctAnswer": 1,
        "pageNumberStart": 20,
        "pageNumberEnd": 20
      });
    
      const [questionLoading, setQuestionLoading] = useState(true);
      const [showQuestionLoadingError, setShowQuestionLoadingError] = useState(false);
      const [chosenAnswer, setChosenAnswer] = useState(-1);
      const [answered, setAnswered] = useState(false);
      const [questionPosition, setQuestionPosition] = useState(0);

      const [correctAnswers, setCorrectAnswers] = useState(0);

      const [quizComplete, setQuizComplete] = useState(false);
      const[questions, setQuestions] = useState<QuestionModel[]>([
        
      ]);


    function containsQuestion(qArr:QuestionModel[], q : QuestionModel) {
      qArr.forEach(qE => {
        if (qE.question === q.question) return true;
      });

      return false;
    }
 
    async function loadQuestion(){
      try {
        setShowQuestionLoadingError(false);
        setQuestionLoading(true);

        let questionArray: string | any[] | ((prevState: QuestionModel[]) => QuestionModel[]) = [];
        

        for (let index = 0; index < props.questionCount; index++) {
          let response = await fetch("/api/questions/" + props.category + "/random", {method: "GET"});

          let q = await response.json();

          if (containsQuestion(questionArray, q)){
            response = await fetch("/api/questions/" + props.category + "/random", {method: "GET"});

            q = await response.json();
          }
          
          questionArray[index] = q;
        }
        
        setQuestions(questionArray);
        console.log(questionArray);
        
        setQuestion(questionArray[0]);
        setAnswered(false);
      } catch (error) {
        console.log(error);
        alert(error)
        setShowQuestionLoadingError(true);
      } finally {
        setQuestionLoading(false);
      }
      
    }
      
  useEffect(() => {
    loadQuestion();
  }, []);

  const errorToast = <Toast>
                      <Toast.Header>
                        <img src="holder.js/20x20?text=%20" className="rounded me-2" alt="" />
                        <strong className="me-auto">Hugo Training Platform</strong>
                      </Toast.Header>
                      <Toast.Body>Something went wrong! Please try refreshing the page</Toast.Body>
                    </Toast>

    return ( 
        <>
            {questionLoading && <Spinner color="secondary" style={{marginLeft:"auto", marginRight:"auto", justifyContent:"center", display:"flex"}}/>}
            {showQuestionLoadingError && errorToast}
            {!questionLoading && !showQuestionLoadingError && !quizComplete && <Question 
            question={questions[questionPosition]}
            liveCorrections={props.liveCorrections}
            currentQuestionCount={props.questionCount - questionPosition}
            showPageNum={props.showPageNum}
            onAnsOneClick={() => {setChosenAnswer(0)}}
            onAnsTwoClick={() => {setChosenAnswer(1)}}
            onAnsThreeClick={() => {setChosenAnswer(2)}}
            onAnsFourClick={() => {setChosenAnswer(3)}}
            onAnsFiveClick={() => {setChosenAnswer(4)}}
            onSubmitClick={(isCorrect: boolean) => {
              if (answered) return;

              setAnswered(true);
              

              if (isCorrect){
                setCorrectAnswers(correctAnswers + 1);

                UserApi.setStat({
                  username: props.user.username,
                  newValue: 1,
                  stat: props.category
                })
              }else{
                UserApi.setStat({
                  username: props.user.username,
                  newValue: 0,
                  stat: props.category
                })
              }

            }}
            onNextQuestionClick={()=> {
              
              setQuestionPosition(questionPosition + 1);
              
              if (questionPosition === props.questionCount - 1){
                setQuizComplete(true);
              }else{
                setAnswered(false);
                
                
                setQuestion(questions[questionPosition + 1]);
              }
            }}
          
            ></Question>}

            {quizComplete && 
              
              <Card style={{justifyContent:"center", marginLeft:"auto", marginRight:"auto", width:"26rem", height:"20rem"}}>
                        <CardHeader className="flex gap-3">
                            <div className="flex flex-col" style={{display:"flex", justifyContent:"center", marginLeft:"auto", marginRight:"auto"}}>
                            <h2 style={{display:"flex", justifyContent:"center", marginLeft:"auto", textAlign:"center", marginRight:"auto", fontSize:40, marginTop:"auto", marginBottom:"0.6rem"}}>Quiz Complete!</h2>
                            
                            </div>
                        </CardHeader>
                        <Divider/>
                        <CardBody>
                            <div className="space-y-1">
                                    <div className="inline-block">
                                    
                                        
                                    </div>
                                    <p className="text-small text-default-400" style={{display:"flex", justifyContent:"center", marginTop:"2rem"}}>Scored {correctAnswers + " Correct Out Of " + props.questionCount}</p>
                                    <p className="text-small text-default-400" style={{display:"flex", justifyContent:"center", marginTop:"2rem"}}>In a Competition That Would Be:</p>
                                    
                                    <p className="text-large text-green-400" style={{display:"flex", justifyContent:"center", marginTop:"2rem"}}>{(correctAnswers/props.questionCount) * 1000}</p>
                                </div>
                        </CardBody>
                        <Divider/>
                    </Card>

              
            }
        </>
        
     );
}
 
export default QuestionPageLoggedInView;