/* eslint-disable eqeqeq */
import styles from "../styles/Question.module.css"
import { Avatar, Button, Card, CardBody, CardFooter, CardHeader, Divider, Dropdown, DropdownItem, DropdownMenu, DropdownTrigger, Link, Navbar, NavbarBrand, NavbarContent, NavbarItem, Radio, RadioGroup, Tab, Tabs, radioGroup } from "@nextui-org/react";
import { Question as QuestionModel } from "../models/question";
import { useEffect, useState } from "react";
import { TabContent } from "react-bootstrap";

interface QuestionProps {
    question: QuestionModel;
    liveCorrections: Boolean;
    currentQuestionCount: Number;
    showPageNum: Boolean;
    onAnsOneClick: () => void;
    onAnsTwoClick: () => void;
    onAnsThreeClick: () => void;
    onAnsFourClick: () => void;
    onAnsFiveClick: () => void;
    onSubmitClick: (correct:boolean) => void;
    onNextQuestionClick: () => void;
}

const Question = ({question, onAnsFiveClick, onAnsFourClick, onAnsOneClick, onAnsThreeClick, onAnsTwoClick, onSubmitClick, onNextQuestionClick, liveCorrections, currentQuestionCount, showPageNum}: QuestionProps) => {
    const [ans, setAns] = useState<Number>(-1);
    const [correctAns, setCorrectAns] = useState<Number>(-1);
    const [notInvalid, setNotInvalid] = useState<Number>(-1);

    const answerArr = ["a", "b", "c", "d", "e"];

    return (

        <>
        <div className="flex w-full flex-col">
            <Card style={{justifyContent:"center", width:"10rem", height:"5rem", marginLeft:"auto", marginRight:"9rem", marginBottom:"-5rem"}}>
                        <CardBody >
                        <p className="font-bold text-inherit text-balance" style={{textWrap:"pretty", marginRight:"auto", marginLeft:"auto", textAlign:"center"}}>{currentQuestionCount + " Question(s) Remaining"}</p>
                        </CardBody>
                    </Card>
        <Tabs aria-label="Options" style={{marginLeft:"auto", marginRight:"auto"}}>
            <Tab key="questionLabel" title="Question">
                <TabContent className="items-baseline">
                    <Card style={{justifyContent:"center", width:"40rem", marginLeft:"auto", marginRight:"auto"}}>
                        <CardBody >
                        <p className="font-bold text-inherit text-balance" style={{textWrap:"pretty", marginRight:"auto", marginLeft:"auto", textAlign:"center"}}>{question.question}</p>
                        </CardBody>
                    </Card>
                </TabContent>
            
            </Tab>
            { showPageNum && <Tab key="page" title="Page Number">
            <TabContent className="items-baseline">
                    <Card style={{justifyContent:"center", width:"10rem", height:"auto", marginLeft:"auto", marginRight:"auto"}}>
                        <CardBody>
                            <h2 style={{justifyContent:"center", marginLeft:"auto", marginRight:"auto", fontSize:45, marginTop:"auto", marginBottom:"0.6rem"}}>{(question.pageNumberEnd == question.pageNumberStart) ? question.pageNumberStart?.toString(): question.pageNumberStart?.toString() + " - " + question.pageNumberEnd?.toString() }</h2>
                        </CardBody>
                    </Card>
                </TabContent> 
            </Tab>}
            
        </Tabs>



        </div>

        <div className="flex justify-center items-center h-3/6" style={{marginBottom:"auto"}}>
        <RadioGroup id="radioGroup" value={ correctAns != -1 && liveCorrections ? (answerArr[parseInt(correctAns.toString())]) : ans != -1 ? answerArr[parseInt(ans.toString())] : ""}>
            <Radio value="a" color={(correctAns != -1 && liveCorrections && correctAns == 0) ? (correctAns == ans ? "success" : "danger") : "default"} onChange={() => {onAnsOneClick(); if (correctAns == -1) setAns(0)}} >{question.answerChoiceA}</Radio>
            <Radio value="b" color={(correctAns != -1 && liveCorrections && correctAns == 1) ? (correctAns == ans ? "success" : "danger") : "default"} onChange={() => {onAnsTwoClick(); if (correctAns == -1) setAns(1)}} >{question.answerChoiceB}</Radio>
            <Radio value="c" color={(correctAns != -1 && liveCorrections && correctAns == 2) ? (correctAns == ans ? "success" : "danger") : "default"} onChange={() => {onAnsThreeClick(); if (correctAns == -1) setAns(2)}} >{question.answerChoiceC}</Radio>
            <Radio value="d" color={(correctAns != -1 && liveCorrections && correctAns == 3) ? (correctAns == ans ? "success" : "danger") : "default"} onChange={() => {onAnsFourClick(); if (correctAns == -1) setAns(3)}} >{question.answerChoiceD}</Radio>
            <Radio value="e" color={(correctAns != -1 && liveCorrections && correctAns == 4) ? (correctAns == ans ? "success" : "danger") : "default"} onChange={() => {onAnsFiveClick(); if (correctAns == -1) setAns(4)}} >{question.answerChoiceE}</Radio>
        </RadioGroup>
        </div>

        <div className="flex justify-center items-center h-3/6 gap-5" style={{marginTop:"16rem", marginLeft:"auto", marginRight:"auto"}}>
        <Button color="primary" variant="shadow" onPress={() => {
                console.log(ans);
                console.log(question.correctAnswer === ans);
                
                onSubmitClick(ans === question.correctAnswer); 
                setCorrectAns(question.correctAnswer);

                if (liveCorrections){
                    if (ans != correctAns){
                        setNotInvalid(correctAns);
                        
                    }
                }

                }}>
            Submit
        </Button>  
       {correctAns != -1 && <Button color="primary" variant="bordered" onPress={() => {
                onNextQuestionClick();

                setNotInvalid(-1);
                setCorrectAns(-1);
                setAns(-1);
                
            }}>
            Continue
        </Button> }
        </div>
    </>
        

        
        
      );
};

