import { useState } from "react";
import {  ButtonGroup, ButtonToolbar, Card, CardSubtitle, Form, ToggleButton } from "react-bootstrap";
import { User } from "../models/user";
import QuestionPageLoggedInView from "./QuestionPageLoggedInView";
import { Button, Checkbox, CheckboxGroup, Input, Select, SelectItem, Switch } from "@nextui-org/react";

interface QuizzesLoggedInProps {
    loggedInUser: User
}

const QuizzesLoggedInView = (props : QuizzesLoggedInProps) => {
    const [liveCorrections, setliveCorrections] = useState(false);
    const [showPageNum, setShowPageNum] = useState(false);
    const [questionCount, setQuestionCount] = useState(25);
    const [category, setCategory] = useState(1);
    const [categoryError, setCategoryError] = useState("");
    const [questionCountError, setQuestionCountError] = useState("");

    const categories = ["socialScience", "science", "econ", "lit", "music", "art", "math"];

    let questionUi = <div>
        <QuestionPageLoggedInView 
            user={props.loggedInUser}
            category={categories[category - 1]}
            questionCount={questionCount} 
            liveCorrections={liveCorrections}      
            showPageNum={showPageNum}      
        />
    </div>

    const [setupActive, setSetupActive] = useState(true);
  
    const setupUi = <>    

  <div className="flex w-full flex-wrap md:flex-nowrap gap-4" style={{display:"flex", justifyContent:"center"}}>
  <CheckboxGroup
    label="Practice Settings"
    defaultValue={[""]}
  >
    <Checkbox value="live-corrections" onValueChange={(selected) => {setliveCorrections(selected)}}>Live Corrections</Checkbox>
    <Checkbox value="page-numbers" onValueChange={(selected) => {setShowPageNum(selected)}}>Page Numbers</Checkbox>

  </CheckboxGroup>

      <Input type="number" errorMessage={questionCountError != "" && questionCountError} label="Question Count" className="w-1/6" onValueChange={(v) => {setQuestionCount(parseInt(v))}} />
      <Select
        label="Testing Category"
        placeholder="Select a Category"
        className="max-w-xs"
        style={{display:"flex"}}
      >

        <SelectItem key="socialScience" value="socialScience" onSelect={() =>{setCategory(0)}}>
            <p className="text-md" style={{color:"white"}}>Social Science</p>
          </SelectItem>
          <SelectItem key="science" value="science" onSelect={() =>{setCategory(1)}}>
            <p className="text-md" style={{color:"white"}}>Science</p>
          </SelectItem>
          <SelectItem key="economics" value="economics" onSelect={() =>{setCategory(2)}}>
          <p className="text-md" style={{color:"white"}}>Economics</p>
          </SelectItem>
          <SelectItem key="literature" value="literature" onSelect={() =>{setCategory(3)}}>
          <p className="text-md" style={{color:"white"}}>Literature</p>
          </SelectItem>
          <SelectItem key="art" value="art" onSelect={() =>{setCategory(4)}}>
          <p className="text-md" style={{color:"white"}}>Art</p>
          </SelectItem>
          
          <SelectItem key="music" value="music" onSelect={() =>{setCategory(5)}}>
          <p className="text-md" style={{color:"white"}}>Music</p>
          </SelectItem>
        
      </Select>

      
      <div className="flex w-full flex-wrap md:flex-nowrap gap-4" style={{justifyContent:"center", display:"flex"}}>
      <Button variant="shadow" color="primary" onClick={() => {
        if (category === undefined){
            
        }

        if(questionCount < 1){
            setQuestionCountError("Question count must be at least 1");
            return
        }else{
            setQuestionCountError("");
        }
        
        setSetupActive(false);
        }}>
        Start Quiz
      </Button>  
    </div>
      
    </div>
  </>

  /*
  <div>
    <h1 style={{color:"#E0FBFC", fontFamily:"Baron"}}>Practice</h1>
    <Card style={{backgroundColor:"#373E40", height:"10rem", width:"50%", marginLeft:"20rem", marginTop:"6rem", paddingTop:"0.3rem", alignContent:"center"}}>
        <Card.Title style={{color:"#E0FBFC", fontFamily:"GothamMedium"}}>Practice Settings</Card.Title>
        <ButtonGroup className="mb-2" style={{marginTop:"0.3rem", height:"2.4rem", width:"50%", marginLeft:"10rem", justifyContent:"normal"}}>
        <ToggleButton
        id="toggle-check"
        type="checkbox"
        variant="outline-success"
        checked={liveCorrections}
        value="1"
        onChange={(e) => setliveCorrections(e.currentTarget.checked)}
        >
        <p style={{fontFamily:"GothamMedium"}}>Live Corrections</p>
        </ToggleButton>

        
    </ButtonGroup>

    <ButtonGroup className="mb-2" style={{marginTop:"0.3rem", height:"2.4rem", width:"50%", marginLeft:"10rem"}}>
    <ToggleButton
        id="toggle-check2"
        type="checkbox"
        variant="outline-success"
        checked={showPageNum}
        value="2"
        onChange={(e) => setShowPageNum(e.currentTarget.checked)}
        >
        <p style={{fontFamily:"GothamMedium"}}>Show Page Number</p>
        </ToggleButton>
    </ButtonGroup>
    </Card>
    <Card style={{backgroundColor:"#373E40", height:"6rem", width:"50%", marginLeft:"20rem", marginTop:"3rem", paddingTop:"0.3rem"}}>
    <Card.Title style={{color:"#E0FBFC", fontFamily:"GothamMedium"}}>Question Amount</Card.Title>
        <ButtonToolbar aria-label="Toolbar with button groups" style={{marginLeft:"11.5rem", marginTop:"0.3rem"}}>
            <ButtonGroup className="me-2" aria-label="First group">
                <Button onClick={() => {setQuestionCount(5)}} style={{backgroundColor:(questionCount == 5 ? "#305252" : "#373E40"), color: "#E0FBFC"}}>5</Button>
            </ButtonGroup>
            <ButtonGroup className="me-2" aria-label="Second group">
                <Button onClick={() => {setQuestionCount(10)}} style={{backgroundColor:(questionCount == 10 ? "#305252" : "#373E40"), color:"#E0FBFC"}}>10</Button>
            </ButtonGroup>
            <ButtonGroup className="me-2" aria-label="Third group">
                <Button onClick={() => {setQuestionCount(15)}} style={{backgroundColor:(questionCount == 15 ? "#305252" : "#373E40"), color:"#E0FBFC"}}>15</Button>
            </ButtonGroup>
            <ButtonGroup className="me-2" aria-label="Fourth group">
                <Button onClick={() => {setQuestionCount(25)}} style={{backgroundColor:(questionCount == 25 ? "#305252" : "#373E40"), color:"#E0FBFC"}}>25</Button>
            </ButtonGroup>
            <ButtonGroup className="me-2" aria-label="Fifth group">
                <Button onClick={() => {setQuestionCount(35)}} style={{backgroundColor:(questionCount == 35 ? "#305252" : "#373E40"), color:"#E0FBFC"}}>35</Button>
            </ButtonGroup>
            <ButtonGroup className="me-2" aria-label="Sixth group">
                <Button onClick={() => {setQuestionCount(50)}} style={{backgroundColor:(questionCount == 50 ? "#305252" : "#373E40"), color:"#E0FBFC"}}>50</Button>
            </ButtonGroup>
        </ButtonToolbar>
    </Card>

    <Card style={{backgroundColor:"#373E40", height:"6rem", width:"50%", marginLeft:"20rem", marginTop:"3rem", paddingTop:"0.3rem"}}>
        <Card.Title style={{color:"#E0FBFC", fontFamily:"GothamMedium"}}>Test Category</Card.Title>
        <Form.Select aria-label="Select Testing Category" onChange={(value : any) => {setCategory(value.target.value)}} style={{backgroundColor:"#373E40", color:"#E0FBFC", marginTop:"0.3rem", fontFamily:"GothamMedium"}}>
        <option value="1">Select Category</option>
        <option value="1">Social Science</option>
        <option value="2">Science</option>
        <option value="3">Economics</option>
        <option value="4">Literature</option>
        <option value="5">Music</option>
        <option value="6">Art</option>

        </Form.Select>
        
    </Card>
    <Button onClick={() => {
        if (category !== undefined){
            setSetupActive(false);
        }
        
        
        }} variant="primary" size="lg" style={{width:"50%", marginTop:"7rem", backgroundColor:"#373E40", color:"#E0FBFC", fontFamily:"GothamMedium"}}>
    Start Quiz
    </Button>
</div>
*/
    
    return ( 
        <>
        {setupActive && setupUi}
        {!setupActive && questionUi}
        </>
     );
}
 
export default QuizzesLoggedInView;