import { Avatar, Button, Card, CardBody, CardFooter, CardHeader, Divider, Dropdown, DropdownItem, DropdownMenu, DropdownTrigger, Link, Navbar, NavbarBrand, NavbarContent, NavbarItem, Radio, RadioGroup, Tab, Tabs } from "@nextui-org/react";
import { User } from "../models/user";
import * as UserApi from "../network/user_api"
import { HugoLogo } from "./HugoLogo";
import { useState } from "react";

interface NavBarLoggedInViewProps {
    user: User,
    onLogoutSuccessful: () => void
}


const NavBarLoggedInView = ({user, onLogoutSuccessful} : NavBarLoggedInViewProps) => {
    const [selectedTab, setSelectedTab] = useState(1);

    async function logout() {
        try {
            await UserApi.logout();
            onLogoutSuccessful();            
        } catch (error) {
            console.log(error);
        }
    }

    return ( 
        <>
            <Navbar style={{backgroundColor:"black", marginBottom:"1rem"}}>
                    <NavbarBrand>
                    <HugoLogo />
                    </NavbarBrand>
                    <NavbarContent className="sm:flex gap-4" justify="center" style={{marginLeft:"4.5rem"}}>
                        <NavbarItem>
                        <Link color="foreground" href="/dashboard">
                            Dashboard
                        </Link>
                        </NavbarItem>
                        <NavbarItem>
                        <Link href="/leaderboard" color="foreground" aria-current="page">
                            Leaderboard
                        </Link>
                        </NavbarItem>
                        <NavbarItem>
                        <Link color="foreground" href="/quizzes">
                            Quizzes
                        </Link>
                        </NavbarItem>
                    </NavbarContent>
                    <NavbarContent justify="end">
                        <NavbarItem className="lg:flex">
                            
                            </NavbarItem>
                            <NavbarItem>
                            <Button as={Link} color="primary" href="" variant="flat" onClick={logout}>
                                Logout
                            </Button>
                        </NavbarItem>
                    </NavbarContent>
                    <Avatar
                                showFallback
                                style={{display:"inline-block", marginLeft:"auto", marginRight:"2rem"}}
                                name={user.username}
                                src=""
                            />
                </Navbar>
        </>

     );

     /*
     <Navbar.Collapse id="basic-navbar-nav">
                    
                <Nav className="me-auto">
                    <Nav.Link href="/dashboard" className="navbarText" style={{marginRight:"1rem"}}>Dashboard</Nav.Link>
                    <Nav.Link href="/leaderboard" className="navbarText" style={{marginRight:"1rem"}}>Leaderboard</Nav.Link>
                    <Nav.Link href="/quizzes" className="navbarText" style={{marginRight:"23rem"}}>Quizzes</Nav.Link>
                    
                </Nav>
                </Navbar.Collapse>

                <Navbar.Collapse className="justify-content-end" style={{color: "#E0FBFC", marginLeft: "3rem"}}>
                <Navbar.Text className="navbarText">
                    Signed in as: {user.username}
                </Navbar.Text>
                
                </Navbar.Collapse>
                <Button style={{marginLeft:"3rem", color:"#E0FBFC", background:"#373E40"}} onClick={logout}>Logout</Button>
                */
}
 
export default NavBarLoggedInView;