import DashboardLoggedInView from "../components/DashboardLoggedInView";
import QuestionPageLoggedOutView from "../components/QuestionPagedLoggedOutView";
import { User } from "../models/user";

interface DashboardPageProps {
    loggedInUser: User | null
}


const DashboardPage = (props : DashboardPageProps) => {
    return ( 
        <>
        {props.loggedInUser ? 
          <DashboardLoggedInView loggedInUser={props.loggedInUser}></DashboardLoggedInView> : <QuestionPageLoggedOutView></QuestionPageLoggedOutView>
        }
        
        </>
        
     );
}
 
export default DashboardPage;