import { useForm } from "react-hook-form";
import { User } from "../models/user";
import { LogInCredentials } from "../network/user_api";
import * as UserApi from "../network/user_api";
import {Modal, ModalContent, ModalFooter, ModalHeader, Button, ModalBody, Input, useDisclosure, Tooltip} from "@nextui-org/react"
import TextInputField from "./form/TextInputField";
import { useState } from "react";

interface LoginModalProps {
    onDismiss: () => void;
    onLoginSuccessful: (user: User) => void;
}

const LoginModal = ({onDismiss, onLoginSuccessful} : LoginModalProps) => {

    const {register, handleSubmit, formState: {errors, isSubmitting}} = useForm<LogInCredentials>();

    async function onSubmit(credentials : LogInCredentials) {
        setSubmitting(true);

        try {
            const user = await UserApi.login(credentials);
            onLoginSuccessful(user);
            console.log("submitted successfully");

        } catch (error: any) {
            if ((error?.message as string).match("Unexpected token")){
                setGeneralError("Server is down, please try again later");
            }else if ((error?.message as string).match("UnauthorizedError")){
                setGeneralError("Incorrect Login Details");
            }else{
                setGeneralError("An error occurred");
            }
            
            console.log(error?.message || "An error occurred");
        } finally {
            setSubmitting(false);
        }
    }

    const [usernameInput, setusernameInput] = useState("");
    const [passwordInput, setPasswordInput] = useState("");
    const [submitting, setSubmitting] = useState(false);

    const [usernameError, setusernameError] = useState("");
    const [passwordError, setPasswordError] = useState("");
    const [generalError, setGeneralError] = useState("");

    return ( 
        <>
        <Modal isOpen={true} onClose={onDismiss}>
            <ModalContent>
            {(onClose) => (
                <>
                <ModalHeader className="flex flex-col gap-1" style={{color:"white"}}>Log In</ModalHeader>
                <ModalBody>
                <Input isInvalid={(usernameError != "")} errorMessage={usernameError != "" && (usernameError)} type="username" label="Username" placeholder="Enter your username" required onChange={(e) => {setusernameInput(e.target.value); setusernameError("");}} />
                <Input isInvalid={(passwordError != "")} errorMessage={passwordError != "" && (passwordError)} type="password" label="Password" placeholder="Enter your password" required onChange={(e) => {setPasswordInput(e.target.value); setPasswordError("");}} />

                </ModalBody>
                <ModalFooter>
                    <Button color="danger" variant="light" onPress={onDismiss}>
                    Close
                    </Button>
                    <Button isLoading={submitting} color="primary" onPress={() => {
                        setGeneralError("");
                        let issue = false;

                        if (usernameInput == ""){
                            setusernameError("Username cannot be empty");
                            issue = true;
                        }

                        if (passwordInput == ""){
                            setPasswordError("Password cannot be empty");
                            issue = true;
                        }

                        if (issue){
                            return;
                        }
                        
                        onSubmit({username: usernameInput, password: passwordInput });

                    }}>
                    Log In
                    </Button>
                    {
                        generalError != "" &&           <Tooltip key="danger" color="danger" className="capitalize" content={<p className="font-bold text-inherit text-balance" style={{textWrap:"pretty", marginRight:"auto", marginLeft:"auto", textAlign:"center"}}>{generalError}</p>}>
                                                            <Button variant="flat" color="danger" className="capitalize">
                                                                Error
                                                            </Button>
                                                        </Tooltip>
                    }
                </ModalFooter>
                
                </>
            )}
            </ModalContent>
        </Modal>
        </>

        

     );
}

